<template>
  <div class="hello">
     <h2>
      {{ ww }}
     </h2>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  computed:{
    ww(){
      console.log(localStorage.getItem('value'),'123132')
      return localStorage.getItem('value')
    }
  }
}
</script>

